import * as d3 from 'd3';
import * as _ from 'lodash';

let nodes = [
    { name: "桂林" },
    { name: "广州" },
    { name: "厦门" },
    { name: "杭州" },
    { name: "上海" },
    { name: "青岛" },
    { name: "天津" }
], links = [
    { source: 0, target: 1 },
    { source: 0, target: 2 },
    { source: 0, target: 3 },
    { source: 1, target: 4 },
    { source: 1, target: 5 },
    { source: 1, target: 6 }
];

export class D3Controller {
    static $inject = ["$stateParams"];

    private simulation: any;
    private link: any;
    private node: any;

    constructor(private $stateParams: ng.ui.IStateParamsService) {
        this.drawSuits();
    }

    doTrans() {
        var newLinks = links.pop();
        this.link.data(links);
        this.node.data(nodes);
        this.simulation.nodes(nodes)
            .force("link", d3.forceLink(links).distance(100)).restart();
    }

    drawSuits() {
        const width = innerWidth || Math.max(960, innerWidth),
            height = innerHeight || Math.max(500, innerHeight);
        let svg = d3.select("#homed3")
            .attr("width", width)
            .attr("height", height)

        let color = d3.scaleOrdinal(d3.schemeCategory20);
        let simulation = this.simulation = d3.forceSimulation()
            .force("link", d3.forceLink().id((d: { name: string }): string => { return d.name; }))
            .force("charge", d3.forceManyBody().strength(-300))
            .force("center", d3.forceCenter(width / 2, height / 2));

        let link = this.link = svg.append("g")
            .attr("class", "links")
            .selectAll("line")
            .data(links)
            .enter()
            .append("line")
            .style("stroke", (d, i: any): string => {
                return color(i);
            })
            .attr("stroke-width", (d) => { return Math.sqrt(1.2) });

        let dragFunc = d3.drag()
            .on("start", dragstarted)
            .on("drag", dragged)
            .on("end", dragended);

        let node = this.node = svg.append("g")
            .attr("class", "nodes")
            .selectAll("circle")
            .data(nodes)
            .enter()
            .append("circle")
            .attr("r", 5)
            .style("fill", (d, i: any): string => {
                return color(i);
            })
            .call(dragFunc);

        simulation.nodes(nodes)
            .on("tick", ticked)
            .force("link", d3.forceLink(links).distance(100));

        function dragstarted(d) {
            if (!d3.event.active) simulation.alphaTarget(0.5).restart();
            d.fx = d.x;
            d.fy = d.y;
        }

        function dragged(d) {
            d.fx = d3.event.x;
            d.fy = d3.event.y;
        }

        function dragended(d) {
            if (!d3.event.active) simulation.alphaTarget(0);
            d.fx = null;
            d.fy = null;
        }

        function ticked() {
            link
                .attr("x1", function (d: any) { return d.source.x; })
                .attr("y1", function (d: any) { return d.source.y; })
                .attr("x2", function (d: any) { return d.target.x; })
                .attr("y2", function (d: any) { return d.target.y; });

            node
                .attr("cx", function (d: any) { return d.x; })
                .attr("cy", function (d: any) { return d.y; });
        }

        return;

        // const width = innerWidth || Math.max(960, innerWidth),
        //     height = innerHeight || Math.max(500, innerHeight);

        // let svg = d3.select("#homed3")
        //     .append("svg")
        //     .attr("width", width)
        //     .attr("height", height);
        // // 通过布局来转换数据，然后进行绘制
        // let simulation = d3.forceSimulation(nodes)
        //     .force("link", d3.forceLink(links).distance(100))
        //     .force("charge", d3.forceManyBody().strength(-300))
        //     .force("center", d3.forceCenter(width / 2, height / 2));

        // let color = d3.scaleOrdinal(d3.schemeCategory20);
        // let marker = svg.append("marker")
        //     .attr("id", function (d: any) { return d; })
        //     .attr("id", "resolved")
        //     .attr("markerUnits", "strokeWidth")//设置为strokeWidth箭头会随着线的粗细发生变化
        //     .attr("markerUnits", "userSpaceOnUse")
        //     .attr("viewBox", "0 -5 10 10")//坐标系的区域
        //     .attr("refX", 32)//箭头坐标
        //     .attr("refY", -1)
        //     .attr("markerWidth", 12)//标识的大小
        //     .attr("markerHeight", 12)
        //     .attr("orient", "auto")//绘制方向，可设定为：auto（自动确认方向）和 角度值
        //     .attr("stroke-width", 2)//箭头宽度
        //     .append("path")
        //     .attr("d", "M0,-5L10,0L0,5")//箭头的路径
        //     .attr('fill', '#ff0000');//箭头颜色
        // let svg_links = svg.selectAll(".edgepath")
        //     .data(links)
        //     .enter()
        //     .append("path")
        //     .attr("d", function (d: any) { return 'M ' + d.source.x + ' ' + d.source.y + ' L ' + d.target.x + ' ' + d.target.y })
        //     .attr("class", "edgepath")
        //     .attr("id", function (d, i) { return 'edgepath' + i; })
        //     .style("stroke", "#B43232")
        //     .style("pointer-events", "none")
        //     .style("stroke-width", 1)
        //     .attr("marker-end", "url(#resolved)");

        // let drag = d3.drag().on("drag", function (d: any) {
        //     d3.select(this)
        //         .attr("cx", d.cx = d3.event.x)
        //         .attr("cy", d.cy = d3.event.y);
        // });
        // let svg_nodes = svg.selectAll("circle")
        //     .data(nodes)
        //     .enter()
        //     .append("circle")
        //     .attr("r", 20)
        //     .style("fill", function (d, i: any) {
        //         return color(i);
        //     })
        //     .attr("cx", function (d: any) { return d.x; })
        //     .attr("cy", function (d: any) { return d.y; });
        // let svg_text = svg.selectAll("text")
        //     .data(nodes)
        //     .enter()
        //     .append("text")
        //     .style("fill", "#000")
        //     .attr("dx", 20)
        //     .attr("dy", 10)
        //     .text(function (d) { return d.name; });

        // function draw() {
        //     svg_nodes
        //         .attr("cx", function (d: any) { return d.x; })
        //         .attr("cy", function (d: any) { return d.y; });

        //     svg_text
        //         .attr("x", function (d: any) { return d.x; })
        //         .attr("y", function (d: any) { return d.y; });

        //     svg_links.attr('d', function (d: any) {
        //         var path = 'M ' + (d.source.x + 5) + ' ' + (d.source.y + 5) + ' L ' + d.target.x + ' ' + d.target.y;
        //         return path;
        //     });
        // }
        // simulation.on("tick", draw);
    }
}


